<template>
  <div>
    <Header />
    <div class="slide_img">
        <v-carousel :show-arrows="false" hide-delimiter-background height="700px">
            <v-carousel-item v-for="(item,i) in items" :key="i" :src="item.src">
                <v-row justify="center">
                    <v-col cols="12" sm="10" md="10" lg="10" class="slide_title">
                        <div class="white--text font-weight-bold title_content">Book your authentic trip,<br>get the unique experience.</div>
                    </v-col>
                </v-row>
                <v-row justify="center">
                    <v-col cols="12" sm="10" md="10" lg="10" class="slide_map">
                        <div class="map_div" style="display:inline-flex; float: right">
                            <img src="../assets/img/outlined.map.png">
                            <div class="white--text caption" style="margin-left: 20px">MOST POPULAR
                                <div class="white--text title">AI-Khazneh,Petra</div>
                                <div class="white--text caption" style="display: inline-flex; margin-top:20px">
                                    <img src="../assets/img/Line 1.png" height="1px" width="20px" style="margin-right: 10px; margin-top:8px">
                                    DISCOVER DESTINATION
                                    <v-btn text style="margin-top: -8px"><img src="../assets/img/dropdown.sign.png"></v-btn>
                                </div>
                            </div>
                        </div>
                    </v-col>
                </v-row>
                <v-row justify="center">
                    <v-col cols="12" md="10" lg="10">
                        <div style="display:inline-flex;margin-top: 60px">
                            <img src="../assets/img/Path 47.png">
                            <div class="white--text caption" style="margin-left: 15px;margin-right: 30px">BOOK A TRIP</div>
                            <img src="../assets/img/Path 48.png">
                            <div class="white--text caption" style="margin-left: 15px">RENT A CAR</div>
                        </div>
                    </v-col>
                </v-row>
            </v-carousel-item>
        </v-carousel>
    </div>
    <div class="search_field">
        <v-row justify="center">
            <v-col cols="10" sm="10" md="10" lg="10">
                <v-row>
                    <v-select :items="where_items" height="80px" placeholder="WHERE" background-color="white"></v-select>
                    <v-menu
                        ref="menu"
                        v-model="menu"
                        :close-on-content-click="false"
                        :return-value.sync="date"
                        transition="scale-transition"
                        offset-y
                        min-width="290px"
                    >
                        <template v-slot:activator="{ on }">
                        <v-text-field
                            v-model="date"
                            label="START DATE"
                            prepend-icon="event"
                            readonly
                            v-on="on"
                            height="80px"
                            background-color="white"
                        ></v-text-field>
                        </template>
                        <v-date-picker v-model="date" no-title scrollable>
                        <v-spacer></v-spacer>
                        <v-btn text color="primary" @click="menu = false">Cancel</v-btn>
                        <v-btn text color="primary" @click="$refs.menu.save(date)">OK</v-btn>
                        </v-date-picker>
                    </v-menu>
                    <v-menu
                        ref="menu1"
                        v-model="menu1"
                        :close-on-content-click="false"
                        :return-value.sync="date1"
                        transition="scale-transition"
                        offset-y
                        min-width="290px"
                    >
                        <template v-slot:activator="{ on }">
                        <v-text-field
                            v-model="date1"
                            label="END DATE"
                            prepend-icon="event"
                            readonly
                            v-on="on"
                            height="80px"
                            background-color="white"
                        ></v-text-field>
                        </template>
                        <v-date-picker v-model="date1" no-title scrollable>
                        <v-spacer></v-spacer>
                        <v-btn text color="primary" @click="menu1 = false">Cancel</v-btn>
                        <v-btn text color="primary" @click="$refs.menu1.save(date1)">OK</v-btn>
                        </v-date-picker>
                    </v-menu>
                    <v-text-field label="TRAVELLERS" height="80px" background-color="white"></v-text-field>
                    <v-btn color="#ffc120" height="80px" style="margin-top:15px"><img src="../assets/img/Path 51.png"></v-btn>
                </v-row>
            </v-col>
        </v-row>
    </div>
    <v-row class="mood">
        <v-col md="1" lg="1">
            <img src="../assets/img/Line 3.png" height="1px" style="margin-top: 20px; margin-right: 10px">
        </v-col>
        <v-col md="10" lg="10">
            <div style="display: inline-flex">   
                <p class="font-weight-bold display-2" color="#50473d">What's your mood?</p>
                <img src="../assets/img/Line 3.png" height="1px" style="margin-top: 20px; margin-left: 40px; margin-right: 40px">
                <p class="font-weight-bold display-2" color="#50473d" style="text0-align: right">Travelling with us</p>
            </div>
        </v-col>
    </v-row>
    <v-row justify="center">
        <v-col md="10" lg="10">
            <v-row>
                <v-col md="8" lg="8">
                        <v-row>
                            <v-col v-for="item in image_items" class="d-flex child-flex" cols="6">
                                <v-card flat tile class="d-flex">
                                    <v-img :src="item.src" aspect-ratio="1.5" class="grey lighten-2">
                                    <template v-slot:placeholder>
                                        <v-row class="fill-height ma-0" align="center" justify="center">
                                            <v-progress-circular indeterminate color="grey lighten-5"></v-progress-circular>
                                        </v-row>
                                    </template>
                                    <p class="white--text font-weight-bold headline" style="margin-top: 50%;margin-left: 40%">Getaway with friends</p>
                                    <div class="white--text caption" style="display: inline-flex;margin-left:50%">
                                        <img src="../assets/img/Line 1.png" height="1px" width="20px" style="margin-right: 10px; margin-top:8px">
                                        86 DESTINATIONS
                                        <v-btn text style="margin-top: -8px"><img src="../assets/img/dropdown.sign.png"></v-btn>
                                    </div>
                                    </v-img>
                                </v-card>
                            </v-col>
                        </v-row>
                </v-col>
                <v-col md="4" lg="4" class="travel_with_us">
                    <v-row>
                        <v-col class="d-flex child-flex">
                            <v-card flat tile class="d-flex">
                                <v-img :src="require('../assets/img/Path 54.png')" class="grey lighten-2" style="text-align:center">
                                    <div style="display:inline-flex;margin-top:50px;">
                                        <img src="../assets/img/Rectangle 9.png">
                                        <p class="title" style="margin-left:20px" align="left">Discover Genuine<br>Experience</p>
                                    </div><br>
                                    <div style="display:inline-flex;margin-top:50px;">
                                        <img src="../assets/img/Rectangle 10.png">
                                        <p class="title" style="margin-left:20px" align="left">Discover Genuine<br>Experience</p>
                                    </div><br>
                                    <div style="display:inline-flex;margin-top:50px;">
                                        <img src="../assets/img/Rectangle 11.png">
                                        <p class="title" style="margin-left:20px" align="left">Discover Genuine<br>Experience</p>
                                    </div>
                                </v-img>
                            </v-card>
                        </v-col>
                    </v-row>
                </v-col>
            </v-row>
        </v-col>
    </v-row>
    <v-row class="programs_title">
        <v-col md="1" lg="1">
            <img src="../assets/img/Line 3.png" height="1px" style="margin-top: 20px; margin-right: 10px">
        </v-col>
        <v-col md="10" lg="10">
            <div style="display: inline-flex">   
                <p class="font-weight-bold display-2" color="#50473d">Featured Programs</p>
            </div>
        </v-col>
    </v-row>
    <v-row>
        <v-col cols="1" sm="1" md="1" lg="1">
            <img src="../assets/img/Group 22.png" height="400px" @click="showPreview" class="text-center">
        </v-col>
        <v-col cols="10" sm="10" md="10" lg="10">
            <VueSlickCarousel :arrows="true" v-bind="settings" class="slick_class">
                <div v-for="item in book_items">
                    <v-img :src="item.src" class="book_item" height="400px">
                    <div class="book_btn" style="background-color: white">
                        <v-btn text outlined><span class="black--text font-weight-bold" style="margin-right:10px">BOOK NOW</span><img src="../assets/img/Path 60.png"></v-btn>
                    </div>
                    </v-img>
                </div>
            </VueSlickCarousel>
        </v-col>
        <v-col cols="1" sm="1" md="1" lg="1">
            <img src="../assets/img/Group 23.png" height="400px" @click="showNext">
        </v-col>
    </v-row>
  </div>
</template>
<script>
import Header from "./Header";
import VueSlickCarousel from 'vue-slick-carousel'
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
import 'vue-slick-carousel/dist/vue-slick-carousel-theme.css'
export default {
    components: {
        Header: Header,
        VueSlickCarousel,
    },
    data() {
        return {
            items: [
                { src: require("../assets/img/header.image.png"),},
                { src: require("../assets/img/header.image.png"),},
                { src: require("../assets/img/header.image.png"),},
                { src: require("../assets/img/header.image.png"),},
            ],
            where_items: ['Singapore', 'Singapore1', 'Singapore2', 'Singapore3' ],
            date: new Date().toISOString().substr(0, 10),
            date1: new Date().toISOString().substr(0, 10),
            menu: false,
            modal: false,
            menu1: false,
            image_items: [
                { src: require("../assets/img/bg1.png"),},
                { src: require("../assets/img/bg2.png"),},
                { src: require("../assets/img/bg3.png"),},
                { src: require("../assets/img/bg4.png"),},
            ],
            book_items: [
                { src: require("../assets/img/book1.png"),},
                { src: require("../assets/img/book2.png"),},
                { src: require("../assets/img/book3.png"),},
                { src: require("../assets/img/book4.png"),},
                
            ],
            settings: {
                "focusOnSelect": true,
                "infinite": false,
                "slidesToShow": 4,
                "slidesToScroll": 1,
                "speed": 500
            }
        };
    },
    methods: {
        showNext() {
            this.$refs.carousel.next()
        },
        showPreview() {
            this.$refs.carousel.prev()
        }
    },
};
</script>
<style lang="scss">
.title_content {
    margin-top: 100px;
    font-size: 45px;
}
.slide_map {
    float: right;
    margin-top: 100px;
}
.map_div {

}
.v-carousel__controls {
    position: absolute;
    bottom: 100px;
    right: 100px !important;
    width: 300px;
    button {
        margin-left: -5px;
        background:transparent;
        height: 20px;
        width: 20px;
    }
    // button:focus{
    //     background: white;
    // }
}
.search_field {
    margin-top: -70px;
    input {
        border: none;
    }
}
.mood {
    margin-top: 100px;;
}
.slick_class {
    width: 100%;
}
.book_item {
}
.text-center {
    text-align: center;
}
</style>


