<template>
  <div id="myheader">
    <v-app-bar
      color="white"
      dense
      height="70px"
    >
      <v-menu offset-y>
        <template v-slot:activator="{ on }">
          <v-btn
            text
            color="transparent"
            v-on="on"
          >
            <span class="black--text">ENG</span>
          </v-btn>
        </template>
        <v-list>
          <v-list-item
            v-for="(item, index) in language_items"
            :key="index"
            @click=""
          >
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
      <img
        src="../assets/img/Group 12.png"
        height="40px"
        width="90px"
      >
      <v-spacer></v-spacer>

      <v-tabs>
        <v-tab>HOME</v-tab>
        <v-tab>DESTINATIONS</v-tab>
        <v-tab>STORIES</v-tab>
        <v-tab>ABOUT US</v-tab>
        <v-tab>HELP</v-tab>
      </v-tabs>

      <v-menu offset-y>
        <template v-slot:activator="{ on }">
          <v-btn
            text
            color="transparent"
            v-on="on"
          >
            <span class="black--text">USD</span>
          </v-btn>
        </template>
        <v-list>
          <v-list-item
            v-for="(item, index) in currency_items"
            :key="index"
            @click=""
          >
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
      <v-menu offset-y>
        <template v-slot:activator="{ on }">
          <v-btn
            text
            color="transparent"
            v-on="on"
          >
            <span class="black--text">SIGN UP</span>
          </v-btn>
        </template>
        <v-list>
          <v-list-item
            v-for="(item, index) in sign_items"
            :key="index"
            @click=""
          >
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
      <v-menu offset-y>
        <template v-slot:activator="{ on }">
          <v-btn
            text
            color="transparent"
            v-on="on"
          >
            <span class="black--text">GET APP</span>
          </v-btn>
        </template>
        <v-list>
          <v-list-item
            v-for="(item, index) in language_items"
            :key="index"
            @click=""
          >
            <v-list-item-title>{{ item.title }}</v-list-item-title>
          </v-list-item>
        </v-list>
      </v-menu>
    </v-app-bar>
  </div>
</template>
<script>
export default {
  data() {
    return {
      language_items: [
        { title: 'ENG' },
        { title: 'GER' },
        { title: 'ESP' },
        { title: 'FRE' },
      ],
      currency_items: [
          { title: 'USD'},
          { title: 'USD'},
          { title: 'USD'},
          { title: 'USD'}
      ],
      sign_items: [
          { title: 'SIGN UP'},
          { title: 'SIGN IN'}
      ]
    };
  }
};
</script>
